.table_header {
    text-align: center;
    margin-top: 10px;
    font-size: 1.3vw;
}

.info { padding: 6px 8px; font: 14px/16px Arial, Helvetica, sans-serif; background: white; background: rgba(255,255,255,0.8); box-shadow: 0 0 15px rgba(0,0,0,0.2); border-radius: 5px; } .info h4 { margin: 0 0 5px; color: #777; }
.legend { text-align: left; line-height: 18px; color: #555; } .legend i { width: 18px; height: 18px; float: left; margin-right: 8px; opacity: 0.7; }
.info.legend {line-height: 1.4;}

.companyName {color: white !important; text-align: right; padding: 10px 10px 0 0}
.companyName > a{color: white !important; text-align: right; padding: 10px 10px 0 0}

div.lineChart div {
    width: 100% 
}

.ui.menu > * {
    font-size: 1.5vw !important;
}

.ui.unstackable.table.celled > * {
    font-size: 1vw !important;
}